import { useState, useRef } from "react";
import { Link, useHistory } from "react-router-dom";
import { Typography, TextField, Button } from "@material-ui/core";
import { makeStyles } from "@material-ui/core/styles";
import { MuiPickersUtilsProvider, DateTimePicker } from "@material-ui/pickers";
import { differenceInMinutes } from "date-fns";
import DateFnsUtils from "@date-io/date-fns";
import { fi } from "date-fns/locale";

const useStyles = makeStyles({
    text: {
        marginTop: "10px",
        marginBottom: "5px",
    },
    box: {
        marginTop: "10px",
        marginBottom: "10px",
        marginLeft: "5px",
        marginRight: "5px",
        width: "35ch",
    },
});
function LisaaSuoritus(props) {
    const history = useHistory();
    const classes = useStyles();
    const kuvaus = useRef();

    const [virheilmoitus, setVirheilmoitus] = useState({});
    const lomaketiedot = useRef({});
    const [aloitus, setAloitus] = useState(null);
    const [lopetus, setLopetus] = useState(null);
    var tanaan = new Date();

    if (!props.suoritukset) return null;

    const inputHandler = (event) => {
        lomaketiedot.current[event.target.name] = event.target.value;
    };

    const formHandler = (event) => {
        event.preventDefault();
        let virheet = {};
        let uusiSuoritus = {
            kuvaus: kuvaus.current.value,
            aloitus: aloitus,
            lopetus: lopetus,
            kesto: differenceInMinutes(lopetus, aloitus),
        };

        if (!lomaketiedot.current.kuvaus) {
            virheet = {
                ...virheet,
                kuvaus: "Kuvaus puuttuu.",
            };
        }

        if (differenceInMinutes(lopetus, aloitus) < 1) {
            virheet = {
                ...virheet,
                lopetus: "Lopetusaika ei voi olla ennen aloitusaikaa.",
            };
        }

        if (differenceInMinutes(tanaan, aloitus) < 1) {
            virheet = {
                ...virheet,
                aloitus: "Aloitusaika ei voi olla tulevaisuudessa.",
            };
        }
        if (differenceInMinutes(tanaan, lopetus) < 1) {
            virheet = {
                ...virheet,
                lopetus: "Lopetusaika ei voi olla tulevaisuudessa.",
            };
        } else {
            setVirheilmoitus({});
        }

        if (Object.entries(virheet).length > 0) {
            setVirheilmoitus({ ...virheet });
        } else {
            setVirheilmoitus({});
            props.setSuoritukset([...props.suoritukset, uusiSuoritus]);
            let kokonaiskestoApu = differenceInMinutes(lopetus, aloitus);
            for (var i = 0; i < props.suoritukset.length; i++) {
                kokonaiskestoApu += props.suoritukset[i].kesto;
            }
            props.setKokonaiskesto(kokonaiskestoApu);
            history.push("/aktiviteetit");
        }
    };

    return (
        <>
            <Typography className={classes.text} align="center" variant="h3">
                Lisää uusi liikuntasuoritus
            </Typography>
            <MuiPickersUtilsProvider utils={DateFnsUtils} locale={fi}>
                <form
                    onSubmit={formHandler}
                    className={classes.form}
                    noValidate
                    autoComplete="off"
                >
                    <TextField
                        label="Liikuntasuorituksen kuvaus"
                        inputRef={kuvaus}
                        required
                        className={classes.box}
                        variant="outlined"
                        id="kuvaus"
                        name="kuvaus"
                        error={Boolean(virheilmoitus.kuvaus)}
                        helperText={virheilmoitus.kuvaus}
                        onChange={inputHandler}
                    />
                    <br />
                    <DateTimePicker
                        required
                        className={classes.box}
                        label="Aloitusaika"
                        inputVariant="outlined"
                        cancelLabel="Peruuta"
                        ampm={false}
                        value={aloitus}
                        format="d.M.y HH:mm"
                        onChange={setAloitus}
                        disableFuture
                        strictCompareDates
                        maxDateMessage="Valittu aika ei voi olla tulevaisuudessa."
                        error={Boolean(virheilmoitus.aloitus)}
                        helperText={virheilmoitus.aloitus}
                        clearable
                        clearLabel="Tyhjennä"
                    />
                    <DateTimePicker
                        required
                        className={classes.box}
                        label="Lopetusaika"
                        inputVariant="outlined"
                        cancelLabel="Peruuta"
                        ampm={false}
                        value={lopetus}
                        format="d.M.y HH:mm"
                        onChange={setLopetus}
                        disableFuture
                        strictCompareDates
                        maxDateMessage="Valittu aika ei voi olla tulevaisuudessa."
                        error={Boolean(virheilmoitus.lopetus)}
                        helperText={virheilmoitus.lopetus}
                    />
                    <br />
                    <Button
                        type="submit"
                        variant="contained"
                        color="primary"
                        style={{
                            marginTop: "10px",
                            marginBottom: "10px",
                            marginLeft: "5px",
                            marginRight: "5px",
                        }}
                    >
                        Lisää suoritus
                    </Button>
                    <Button
                        component={Link}
                        to="/"
                        variant="contained"
                        color="secondary"
                        style={{
                            marginTop: "10px",
                            marginBottom: "10px",
                            marginLeft: "5px",
                            marginRight: "5px",
                        }}
                    >
                        Peruuta
                    </Button>
                    <br />
                </form>
            </MuiPickersUtilsProvider>
            <br />
        </>
    );
}

export default LisaaSuoritus;
