import {
    List,
    ListItem,
    ListItemText,
    Typography,
    IconButton,
    ListItemIcon,
    Button,
} from "@material-ui/core";

import { format } from "date-fns";
import { Link } from "react-router-dom";
import DeleteIcon from "@material-ui/icons/Delete";
import { makeStyles } from "@material-ui/core/styles";
import AddIcon from "@material-ui/icons/Add";
import Fab from "@material-ui/core/Fab";

const useStyles = makeStyles({
    text: {
        marginTop: "15px",
        marginBottom: "10px",
        textAlign: "center",
    },
});
function BongatutLinnut(props) {
    const classes = useStyles();

    props.linnut.sort(function (a, b) {
        return new Date(b.aika) - new Date(a.aika);
    });

    return (
        <>
            <Typography className={classes.text} variant="h3">
                Kaikki bongaamasi linnut
            </Typography>
            <List>
                {props.linnut.map((lintu, idx) => {
                    return (
                        <ListItem key={idx}>
                            <ListItemText
                                primary={lintu.lintulaji}
                                secondary="Lintulaji"
                            />
                            <ListItemText
                                primary={lintu.paikka}
                                secondary="Havaintopaikka"
                            />
                            <ListItemText
                                primary={format(lintu.aika, "d.M.y")}
                                secondary="Havaintopäivä"
                            />
                            <ListItemText
                                primary={format(lintu.aika, "HH:mm")}
                                secondary="Havaintoaika"
                            />
                            <ListItemIcon>
                                <Button
                                    component={Link}
                                    to={`/muokkaalintua/${lintu.id}`}
                                    variant="contained"
                                    //color="secondary"
                                    style={{
                                        marginTop: "10px",
                                        marginBottom: "10px",
                                        marginLeft: "5px",
                                        marginRight: "5px",
                                    }}
                                >
                                    Muokkaa
                                </Button>
                            </ListItemIcon>

                            <ListItemIcon>
                                <IconButton
                                    edge="end"
                                    component={Link}
                                    to={`/poistalintu/${lintu.id}`}
                                >
                                    <DeleteIcon />
                                </IconButton>
                            </ListItemIcon>
                        </ListItem>
                    );
                })}
            </List>
            <Fab
                color="primary"
                aria-label="add"
                component={Link}
                to="/lisaalintu"
            >
                <AddIcon />
            </Fab>
        </>
    );
}

export default BongatutLinnut;
