import { Typography } from "@material-ui/core";
import Fab from "@material-ui/core/Fab";
import AddIcon from "@material-ui/icons/Add";
import { Link } from "react-router-dom";
import { differenceInMinutes } from "date-fns";

function Aloitus(props) {
    let kokonaiskesto = 0;
    let viikkokesto = 0;
    for (var i = 0; i < props.suoritukset.length; i++) {
        if (
            differenceInMinutes(new Date(), props.suoritukset[i].lopetus) <
            10081
        ) {
            viikkokesto += props.suoritukset[i].kesto;
        } else {
            viikkokesto += 0;
        }
    }
    for (var j = 0; j < props.suoritukset.length; j++) {
        kokonaiskesto += props.suoritukset[j].kesto;
    }
    return (
        <>
            <Typography align="center" variant="h2">
                Yhteenveto
            </Typography>
            <br />
            <Typography>
                Aktiviteetteihin käytetty aika viimeisen 7 vuorokauden osalta
                on:
            </Typography>
            <Typography variant="h5">
                {`${Math.floor(Number(viikkokesto / 60))} tuntia ja ${Number(
                    viikkokesto % 60
                )} minuuttia `}
            </Typography>
            <br />
            <Typography>
                Aktiviteetteihin käytetty kokonaisaikasi on:
            </Typography>
            <Typography variant="h5">
                {`${Math.floor(Number(kokonaiskesto / 60))} tuntia ja ${Number(
                    kokonaiskesto % 60
                )} minuuttia `}
            </Typography>
            <br />
            <Fab
                color="primary"
                aria-label="add"
                component={Link}
                to="/lisaa-aktiviteetti"
            >
                <AddIcon />
            </Fab>
        </>
    );
}

export default Aloitus;
