import {
    List,
    ListItem,
    ListItemText,
    Typography,
    IconButton,
    ListItemIcon,
} from "@material-ui/core";

import { format } from "date-fns";
import { Link } from "react-router-dom";
import DeleteIcon from "@material-ui/icons/Delete";

import { makeStyles } from "@material-ui/core/styles";
import AddIcon from "@material-ui/icons/Add";
import Fab from "@material-ui/core/Fab";

const useStyles = makeStyles({
    text: {
        marginTop: "15px",
        marginBottom: "10px",
        textAlign: "center",
    },
});
function Aktiviteetit(props) {
    const classes = useStyles();

    props.suoritukset.sort(function (a, b) {
        return new Date(b.lopetus) - new Date(a.lopetus);
    });

    return (
        <>
            <Typography className={classes.text} variant="h3">
                Kaikki liikuntasuorituksesi
            </Typography>
            <List>
                {props.suoritukset.map((suoritus, idx) => {
                    return (
                        <ListItem key={idx}>
                            <ListItemText
                                primary={suoritus.kuvaus}
                                secondary="Kuvaus"
                            />
                            <ListItemText
                                primary={format(
                                    suoritus.aloitus,
                                    "d.M.y HH:mm"
                                )}
                                secondary="Aloitusaika"
                            />
                            <ListItemText
                                primary={format(
                                    suoritus.lopetus,
                                    "d.M.y HH:mm"
                                )}
                                secondary="Lopetusaika"
                            />
                            <ListItemText
                                primary={`${Math.floor(
                                    Number(suoritus.kesto / 60)
                                )} tuntia ja ${Number(
                                    suoritus.kesto % 60
                                )} minuuttia `}
                                secondary="Kesto"
                            />
                            <ListItemIcon>
                                <IconButton
                                    edge="end"
                                    component={Link}
                                    to={`/poista-aktiviteetti/${idx}`}
                                >
                                    <DeleteIcon />
                                </IconButton>
                            </ListItemIcon>
                        </ListItem>
                    );
                })}
            </List>
            <Fab
                color="primary"
                aria-label="add"
                component={Link}
                to="/lisaa-aktiviteetti"
            >
                <AddIcon />
            </Fab>
        </>
    );
}

export default Aktiviteetit;
