import { useState, useRef } from "react";
import { Link, useHistory } from "react-router-dom";
import { Typography, TextField, Button } from "@material-ui/core";
import { makeStyles } from "@material-ui/core/styles";
import { MuiPickersUtilsProvider } from "@material-ui/pickers";
import DateFnsUtils from "@date-io/date-fns";
import { fi } from "date-fns/locale";
import { v4 as uuid } from "uuid";

const useStyles = makeStyles({
    text: {
        marginTop: "10px",
        marginBottom: "5px",
    },
    box: {
        marginTop: "10px",
        marginBottom: "10px",
        marginLeft: "5px",
        marginRight: "5px",
        width: "35ch",
    },
});
function LisaaLintu(props) {
    const history = useHistory();
    const classes = useStyles();
    const [virheilmoitus, setVirheilmoitus] = useState({});
    const lomaketiedot = useRef({});

    const inputHandler = (event) => {
        lomaketiedot.current[event.target.name] = event.target.value;
    };

    const formHandler = (event) => {
        event.preventDefault();
        let virheet = {};
        let aikaApu = new Date();
        let uusiLintu = {
            id: uuid(),
            lintulaji: lomaketiedot.current.lintulaji,
            paikka: lomaketiedot.current.paikka,
            aika: aikaApu.getTime(),
        };

        if (!lomaketiedot.current.lintulaji) {
            virheet = {
                ...virheet,
                lintulaji: "Lintulaji puuttuu.",
            };
        }
        if (!lomaketiedot.current.paikka) {
            virheet = {
                ...virheet,
                paikka: "Paikka puuttuu.",
            };
        } else {
            setVirheilmoitus({});
        }

        if (Object.entries(virheet).length > 0) {
            setVirheilmoitus({ ...virheet });
        } else {
            setVirheilmoitus({});
            props.setLinnut([...props.linnut, uusiLintu]);
            history.push("/");
        }
    };

    return (
        <>
            <Typography className={classes.text} align="center" variant="h3">
                Lisää bongattu lintu
            </Typography>
            <MuiPickersUtilsProvider utils={DateFnsUtils} locale={fi}>
                <form
                    onSubmit={formHandler}
                    className={classes.form}
                    noValidate
                    autoComplete="off"
                >
                    <TextField
                        label="Havaittu lintu"
                        required
                        className={classes.box}
                        variant="outlined"
                        id="lintulaji"
                        name="lintulaji"
                        error={Boolean(virheilmoitus.lintulaji)}
                        helperText={virheilmoitus.lintulaji}
                        onChange={inputHandler}
                    />
                    <TextField
                        label="Havainnon paikka"
                        required
                        className={classes.box}
                        variant="outlined"
                        id="paikka"
                        name="paikka"
                        error={Boolean(virheilmoitus.paikka)}
                        helperText={virheilmoitus.paikka}
                        onChange={inputHandler}
                    />
                    <br />
                    <br />
                    <Button
                        type="submit"
                        variant="contained"
                        color="primary"
                        style={{
                            marginTop: "10px",
                            marginBottom: "10px",
                            marginLeft: "5px",
                            marginRight: "5px",
                        }}
                    >
                        Lisää lintu
                    </Button>
                    <Button
                        component={Link}
                        to="/"
                        variant="contained"
                        color="secondary"
                        style={{
                            marginTop: "10px",
                            marginBottom: "10px",
                            marginLeft: "5px",
                            marginRight: "5px",
                        }}
                    >
                        Peruuta
                    </Button>
                    <br />
                </form>
            </MuiPickersUtilsProvider>
            <br />
        </>
    );
}

export default LisaaLintu;
