import { Button, Typography } from "@material-ui/core";
import { Link, useHistory, useParams } from "react-router-dom";
import { format } from "date-fns";

function PoistaSuoritus(props) {
    const history = useHistory();
    const { indeksi } = useParams();

    const formHandler = (event) => {
        event.preventDefault();

        let suorituksetApu = props.suoritukset.filter((suoritus, idx) => {
            return idx !== Number(indeksi);
        });

        props.setSuoritukset([...suorituksetApu]);

        history.push("/");
    };

    return (
        <form onSubmit={formHandler}>
            <Typography
                variant="h3"
                style={{ marginBottom: 10, marginTop: 10 }}
            >
                Haluatko varmasti poistaa suorituksen:
            </Typography>

            <Typography style={{ marginBottom: 10, marginRight: 10 }}>
                Kuvaus: {props.suoritukset[indeksi].kuvaus}
                <br />
                Aloitus:{" "}
                {format(props.suoritukset[indeksi].aloitus, "d.M.y HH:mm")}
                <br />
                Lopetus:{" "}
                {format(props.suoritukset[indeksi].lopetus, "d.M.y HH:mm")}
                <br />
                Kesto:{" "}
                {`${Math.floor(
                    Number(props.suoritukset[indeksi].kesto / 60)
                )} tuntia ja ${Number(
                    props.suoritukset[indeksi].kesto % 60
                )} minuuttia `}
            </Typography>

            <Button
                type="submit"
                variant="contained"
                color="secondary"
                style={{ marginBottom: 10, marginRight: 10 }}
            >
                Poista suoritus
            </Button>

            <Button
                component={Link}
                to="/"
                variant="contained"
                color="default"
                style={{ marginBottom: 10, marginRight: 10 }}
            >
                Peruuta
            </Button>
        </form>
    );
}

export default PoistaSuoritus;
