import { useState } from "react";
import { Link } from "react-router-dom";
import {
	AppBar,
	Toolbar,
	IconButton,
	CssBaseline,
	Typography,
	Drawer,
	List,
	ListItem,
	ListItemText,
	ListItemIcon,
} from "@material-ui/core";
import MenuIcon from "@material-ui/icons/Menu";
import HomeIcon from "@material-ui/icons/Home";

import SportsBasketballIcon from "@material-ui/icons/SportsBasketball";
import WbSunnyIcon from "@material-ui/icons/WbSunny";
import Icon from "@mdi/react";
import { mdiFish, mdiBinoculars, mdiHomeGroup, mdiTshirtCrew } from "@mdi/js";

import { makeStyles } from "@material-ui/core/styles";

const useStyles = makeStyles((theme) => ({
	button: {
		marginTop: "10px",
		marginBottom: "5px",
		fontSize: "1.3em",
	},
	navigaatio: {
		flexGrow: 1,
		boxShadow: "none",
	},
}));

function Navigation(props) {
	const classes = useStyles();
	const [aukinainenSovellus, setAukinainenSovellus] = useState("");
	const [open, setOpen] = useState(false);
	const toggleDrawer = () => {
		setOpen((value) => !value);
	};
	const handleClick = (x) => {
		setAukinainenSovellus(x);
	};

	return (
		<CssBaseline>
			<AppBar className={classes.navigaatio} position="static">
				<Toolbar>
					<IconButton
						edge="start"
						className={classes.menuButton}
						color="inherit"
						aria-label="menu"
						onClick={toggleDrawer}
					>
						<MenuIcon />
					</IconButton>
					<Typography variant="h6" className={classes.title}>
						Portfolio | {aukinainenSovellus}
					</Typography>
				</Toolbar>
			</AppBar>
			<Drawer anchor="left" open={open} onClose={toggleDrawer}>
				<List
					style={{
						width: "300px",
						marginTop: "20px",
					}}
					onClick={toggleDrawer}
				>
					<ListItem
						onClick={() => {
							handleClick("Etusivu");
						}}
						button
						component={Link}
						to="/portfolio"
					>
						<ListItemIcon>
							<HomeIcon />
						</ListItemIcon>
						<ListItemText primary="Etusivu"></ListItemText>
					</ListItem>

					<ListItem
						onClick={() => {
							handleClick("Lintubongaus");
						}}
						button
						component={Link}
						to="/kaikkilinnut"
					>
						<ListItemIcon>
							<Icon path={mdiBinoculars} size={1} />
						</ListItemIcon>
						<ListItemText primary="Lintubongaus"></ListItemText>
					</ListItem>
					<ListItem
						onClick={() => {
							handleClick("Sääennuste");
						}}
						button
						component={Link}
						to="/nykytila"
					>
						<ListItemIcon>
							<WbSunnyIcon />
						</ListItemIcon>
						<ListItemText primary="Sääennuste"></ListItemText>
					</ListItem>

					<ListItem
						onClick={() => {
							handleClick("Aktiviteettiseuranta");
						}}
						button
						component={Link}
						to="/aktiviteetit"
					>
						<ListItemIcon>
							<SportsBasketballIcon />
						</ListItemIcon>
						<ListItemText primary="Aktiviteettiseuranta"></ListItemText>
					</ListItem>
					<ListItem
						onClick={() => {
							handleClick("Pilkkikisa");
						}}
						button
						component={Link}
						to="/pilkkikisa"
					>
						<ListItemIcon>
							<Icon path={mdiFish} size={1} />
						</ListItemIcon>
						<ListItemText primary="Pilkkikisa"></ListItemText>
					</ListItem>
					<ListItem
						onClick={() => {
							handleClick("Mökkivuokraus");
						}}
						button
						component={Link}
						to="/mokkivuokraus"
					>
						<ListItemIcon>
							<Icon path={mdiHomeGroup} size={1} />
						</ListItemIcon>
						<ListItemText primary="Mökkivuokraus"></ListItemText>
					</ListItem>
					<ListItem
						onClick={() => {
							handleClick("Paitakauppa");
						}}
						button
						component={Link}
						to="/paitakauppa"
					>
						<ListItemIcon>
							<Icon path={mdiTshirtCrew} size={1} />
						</ListItemIcon>
						<ListItemText primary="Paitakauppa"></ListItemText>
					</ListItem>
				</List>
			</Drawer>
		</CssBaseline>
	);
}

export default Navigation;
